 serif;\n  font-size: 12px;\n}\n\nul {\n  margin-top: 40px;\n}\n\nnav {\n  background-color: #FF9B50;\n  box-shadow: 0 0 0.8em #000;\n  color: #fff;\n  position: fixed;\n  top: 0;\n  width: 100%;\n  min-width: 44px;\n  min-height: 44px;\n  padding: 10px;\n  z-index: 1000;\n  display: grid;\n  grid-template-columns: auto auto;\n  align-items: center;\n}\n\n.logo {\n  margin-left: 20px;\n}\n\n.logo-txt {\n  font-size: 30px;\n  font-weight: bold;\n  color: #C63D2F;\n}\n\n.logo-container {\n  display: flex;\n  align-items: center;\n\n  .logo-txt {\n    line-height: 75px;\n    padding-left: 10px;\n  }\n}\n\n.nav-list {\n  position: fixed;\n  width: 100%;\n  height: 0vh;\n  top: 75px;\n  right: 20px;\n  background-color: rgba(255, 187, 92, 5);\n  text-align: center;\n  transition: all .5s;\n  grid-column: span 2;\n\n  li {\n    opacity: 0;\n    line-height: 30px;\n    margin: 50px 0;\n    transition: all .5s;\n    padding: 10px;\n\n    a {\n      color: #FFf;\n      font-size: 20px;\n      min-width: 44px;\n      min-height: 44px;\n      text-transform: uppercase;\n\n      &.active, &:hover {\n        color: #ff5722;\n        transition: 0.5s;\n      }\n    }\n  }\n}\n\n.bellybites-btn {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  font-size: 30px;\n  min-width: 64px;\n  min-height: 64px;\n  padding: 10px;\n  color: #C63D2F;\n  line-height: 75px;\n  cursor: pointer;\n  background-color: transparent;\n  border: none;\n}\n\n.active[type=\"checkbox\"] {\n  width: 45px;\n  height: 45px;\n  margin: 0;\n  padding: 10px;\n  appearance: none;\n  background-color: transparent;\n  border: 2px solid #ff5722;\n  border-radius: 5px;\n  cursor: pointer;\n}\n\n.active[type=\"checkbox\"]:checked {\n  background-color: #ff5722;\n}\n\n#check {\n  display: none;\n  padding: 10px;\n}\n\n#check:checked ~ .nav-list {\n  height: 100vh;\n  width: 100%;\n  padding: 10px;\n\n  li {\n    opacity: 1;\n  }\n}\n\n.bellybites-btn:focus #check:checked ~ .nav-list {\n  height: 0;\n  li {\n    opacity: 0;\n  }\n}\n\n.hero {\n  display: grid;\n  align-items: center;\n  min-height: 400px;\n  width: 100%;\n  text-align: center;\n  background-image: url('../public/images/heros/hero-image_4.jpg');\n  background-position: center;\n  background-color: #FFBB5C;\n  grid-template-columns: 1fr; \n}\n\n.hero__inner {\n  margin: 0 auto;\n  max-width: 800px;\n  margin-top: 20px;\n}\n\n.hero__title {\n  margin: 20px;\n  color: #C63D2F;\n  font-weight: 500;\n  font-size: 36px;\n  border: 2px solid #ff5722;\n  background-color: #FFBB5C;\n  border-radius: 10px;\n}\n\n.hero__tagline {\n  color: #fff;\n  margin-top: 16px;\n  font-size: 18px;\n  font-weight: 300;\n}\n\n\nmain {\n  width: 100%;\n  margin: 0 auto;\n}\n\n.card {\n  display: flex;\n  flex-direction: column;\n  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);\n  border-radius: 5px;\n  padding: 20px;\n\n  background-color: transparent;\n}\n\n.section-title {\n  margin: 20px;\n  text-align: center;\n  color: #C63D2F;\n  font-size: 20px;\n}\n\n.restaurant-img-desc {\n  display: grid;\n  grid-template-columns: 1fr;\n  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);\n  gap: 20px;\n  padding: 10px;\n}\n\n.restaurant-img-desc p {\n  font-size: 15px;\n  color: black;\n  padding: 10px;\n}\n\n.restaurant-img-desc h4 {\n  padding: 10px;\n  font-size: 18px;\n}\n\n\n.content {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 20px;\n  align-items: center;\n  padding: 20px;\n}\n\n.restaurant-image img {\n  width: 100%;\n  border-radius: 10px;\n}\n\n.headline__figure figcaption {\n  text-align: center;\n  color: #C63D2F;\n  font-size: 13px;\n  font-weight: 400;\n  margin-top: 8px;\n}\n\n.headline__content {\n  grid-column: 2 / 3;\n}\n\n.headline__title,\n.restaurant-info h2 {\n  color: #C63D2F;\n  padding: 10px;\n  margin: 0;\n}\n\n.headline__description,\n.restaurant-info {\n  margin-top: 10px;\n}\n\n.content__heading {\n  font-size: 35px;\n  color: #C63D2F;\n}\n\n.restaurant-info h3,\n.restaurant-menu h3, \n.restaurant-reviews h3{\n  padding: 20px;\n  text-align: center;\n  font-size: 25px;\n}\n\n.restaurant-name {\n  font-size: 30px;\n  color: #C63D2F;\n  text-align: center;\n  margin: 20px;\n}\n\n.restaurant-detail-name {\n  min-height: 44px;\n  min-width: 44px;\n  padding: 10px;\n  color: #ff5722;\n}\n\n.restaurant-detail-name:hover {\n  color: #C63D2F;\n}\n\n.restaurant-info h4 {\n  padding: 10px;\n  font-size: 18px;\n}\n\n.headline__button {\n  text-transform: uppercase;\n  margin-top: 24px;\n  cursor: pointer;\n  padding: 8px 16px;\n  color: white;\n  border: none;\n  border-radius: 5px;\n  background-color: #C63D2F;\n  transition: opacity 0.3s;\n}\n\n.headline__button:hover {\n  opacity: 0.8;\n}\n\n.content-restourant {\n  display: grid;\n  grid-template-columns: 1fr;\n  box-shadow: 0 2px 8px 2px rgba(0, 0, 0, 0.2);\n  gap: 20px;\n  align-items: center;\n  padding: 20px;\n}\n\n.restaurant-image {\n  position: relative;\n}\n\n.restaurant-details {\n  position: absolute;\n  top: 10px;\n  left: 10px;\n  display: flex;\n  flex-direction: column;\n  background-color: #FF9B50;\n  padding: 5px;\n  border-radius: 5px;\n  color: #fff;\n  z-index: 1;\n}\n\n.rating {\n  margin-top: 5px;\n}\n\n.input-group {\n  display: grid;\n  grid-template-columns: 3fr 1fr;\n  gap: 20px;\n  padding: 20px;\n}\n\n\n\n.list-restoran {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));\n  gap: 20px;\n  align-items: flex-start;\n  padding: 20px;\n  border-radius: 5px;\n}\n\n.content-detail {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 20px;\n  align-items: center;\n  padding: 20px;\n}\n\n.content__header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  margin-bottom: 20px;\n}\n\n.back-button {\n  text-transform: uppercase;\n  padding: 8px 16px;\n  background-color: #C63D2F;\n  color: white;\n  border: none;\n  border-radius: 5px;\n  cursor: pointer;\n  transition: opacity 0.3s;\n  padding: 10px;\n}\n\n.back-button:h